<template>
    <div class="home">
      <canvas id="bgImgs"></canvas>
      <div id="bgfont">
        <page-one />
    </div>
    </div>
  </template>
  
  <script>
  import pageOne from '@/views/pageOne.vue'
  export default {
    components: {
        pageOne
    },
    data() {
      return {
        w : window.innerWidth,
        h : window.innerHeight,
        showWidth:0,
        yumaos:[],
        context:null,
        img:new Image(),
      }
    },
    mounted() {
        this.initCancas()
        const that = this
        window.onresize = () =>{
            return(() =>{
                that.w = document.body.clientWidth
                this.initCancas()
            })()
        }
    },
    methods:{
        initCancas(){
            let canvas = document.getElementById('bgImgs')
            this.context = canvas.getContext('2d');
            canvas.width = this.w
            canvas.height = this.h
            // let img = new Image();
            this.img.src = require("../assets/img/yumao.png");
            // this.img.rotate(Math.PI/4)

            for(let i  = 0;i<this.num;i++){
                this.yumaos.push({
                    x:Math.random()*this.w,
                    y:Math.random()*this.h,
                    r:Math.random()*10+1,
                })
            }
            this.draw();
            setInterval(this.draw,30)
        },
        move(){
            for(let i  = 0;i<this.num;i++){
                    let yumao = this.yumaos[i]
                    yumao.x += this.getIndex()*2 +1
                    yumao.y += Math.random()*2+1
                    if(yumao.x > this.w){
                        yumao.x = 0
                    }
                    if(yumao.y > this.h){
                        yumao.y = 0
                    }
                }
        },
        draw(){
            this.context.clearRect(0,0,this.w,this.h);
            this.context.beginPath();

                for(let i  = 0;i<this.num;i++){
                    let yumao = this.yumaos[i]
                    this.context.moveTo(yumao.x,yumao.y)
                    this.context.drawImage(this.img,yumao.x, yumao.y, yumao.r*7.5, yumao.r*5);
                }
                this.context.fill();
                this.context.closePath()
                this.move();
        },
        getIndex(){
            let num = Math.random()

            if(Math.round(num *10)/2 == 0) {
                return num*-4
            }else {
                return num*2
            }
        }
    }
  };
  </script>

  <style lang="scss" scoped>
  .home {
    position: relative;
    width: 100%;
    color: #fff;
    #bgImgs {
        position: fixed;
        top: 0;
        left: 0;
        z-index: 0;
        // background: url(../assets/img/pattern.jpg);
        // background: url(../assets/img/bg.png) no-repeat;
        // background: url(../assets/img/bgtt.jpg) no-repeat;
        background: url(../assets/img/25e05e3a055745b017c9a4e45e7d7c2.png) no-repeat;
        // background: url(../assets/img/b0fa1fb3b88e122feb8555ce3a0dbe1.jpg) no-repeat;
        // background-color: #444;

        background-size: cover;
        
    }
    #bgfont {
        width: inherit;
        background: transparent;
        position: absolute;
    }
  }
</style>